import React, {useEffect, useState} from "react"

import { isMobileOnly } from "react-device-detect";

import SignUpDialog from "../../sign-up-dialog/sign-up-dialog"

import "./intro-sections.scss"

const IntroSection = () => {
    const [mobile,setMobile] = useState(false);

    useEffect(() => {
        setMobile(isMobileOnly);
    }, []);

    const [openDialog, setOpenDialog] = useState(false);

    const openDialogHandler = () => {
      setOpenDialog(true);
    };
    const closeDialogHandler = () => {
      setOpenDialog(false);
    }
    const initialFormState = {
        email: ''
    }
    const [formState, setFormState] = useState(initialFormState);
    const inputChangedHandler = event => {
        const { name, value } = event.target;
        setFormState({ ...formState, [name]: value });
    }

    let buttonTitle = mobile ? 'Join the waiting list' : 'Get your early access';

    return (
        <section className={`intro-section ${mobile ? 'mobile' : ''}`}>
            <div className="form">
                <div className="row">
                    <input
                      className="input"
                      type="email"
                      name="email"
                      placeholder="Enter your email address"
                      value={formState.email || ''}
                      onChange={inputChangedHandler}/>
                </div>
                <div className="row">
                    <button className="btn" id="introButton" onClick={openDialogHandler}>
                        { buttonTitle }
                    </button>
                </div>
                <SignUpDialog
                  openDialog={openDialog}
                  closeDialog={closeDialogHandler}
                  emailValue={formState.email}>
                </SignUpDialog>
            </div>
        </section>
    )
}

export default IntroSection
