import React, {useContext, useState, useEffect } from "react"

import axios from 'axios';
import { isMobileOnly } from "react-device-detect";

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CancelRoundedIcon from '@material-ui/icons/CancelRounded';

import './sign-up-dialog.scss'

function makeid(length) {
   var result           = '';
   var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-';
   var charactersLength = characters.length;
   for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
   }
   return result;
}

const SignUpDialog = (props) => {
    const initialFormState = {
        firstName: '',
        lastName: '',
        email: ''
      }
    const [formState, setFormState] = useState(initialFormState);
    const [formSubmitted, setFormSubmitted] = useState(false);
    const [errors,setErrors] = useState({});

    useEffect(() => {
      if (props.emailValue) {
        setFormState({...formState, email: props.emailValue});
      }
    }, [props]);

    useEffect(() => {
      if (props.openDialog) {

        setTimeout(function() {
          const el = document.getElementById('first-name-id')
          el.blur(); //close the keyboard
          el.focus(); //focus on the input
        }, 100);

      }
    }, [props.openDialog])

    const inputChangedHandler = event => {
      const { name, value } = event.target;
      setFormState({ ...formState, [name]: value });
      setErrors({ ...errors, [name]: null});
    }

    const validateForm = (values) => {
      let errors = {};
      const emailRegex = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;

      if (values.first.trim() === '') {
        errors.firstName = 'First name is required';
      }

      if (values.last.trim() === '') {
        errors.lastName = 'Last name is required';
      }

      if (values.email.trim() === '') {
        errors.email = 'Email is required';
      } else if (!emailRegex.test(values.email)) {
        errors.email = 'Email address is invalid';
      }

      return errors;
    }

    const postDataHandler = () => {
      const data = {
        first: formState.firstName,
        last: formState.lastName,
        email: formState.email,
        id: makeid(40)
      }

      if ( Object.keys(validateForm(data)).length === 0 ) {
        axios.post('https://prod-api.spiral.us/register/wait', data)
          .then(response => {
            setFormState({
              first: response.data.first,
              last: response.data.last,
              email: response.data.email
            });
            setFormSubmitted(true);
            if (response.data === true) {

                mixpanel.identify(data.id);
                mixpanel.register({
                  "$email":data.email
                  });
                mixpanel.people.set({ "$email":data.email });
                mixpanel.track('Signup form submitted', {"$email":data.email});

                if (typeof window !== "undefined") {
                    if (window.fbq != null) {
                      window.fbq('track', 'Lead');
                    }
                }
            }
        });
      } else {
        setErrors(validateForm(data));
      }

      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    }

    let title = null;
    let button = null;
    let txtContent = null;
    let dialogForm = null;
    let closeButton = null;
    let submitButtonText = isMobileOnly ? 'Join the waiting list' : 'Get your early access';

    if (formSubmitted) {
        title = (<div>
                    <div>Congratulations</div>
                    <div>You are on the list</div>
                </div>);
        txtContent = (<DialogContentText className="txt">We just sent you a confirmation email with your early access information</DialogContentText>);
        button = (<Button
                    className="btn"
                    onClick={() => {
                      props.closeDialog();
                      setTimeout(() => setFormSubmitted(false), 100)}}>
                        Got it thanks!
                  </Button>);
    } else {
        closeButton= (<IconButton aria-label="close" onClick={() => {props.closeDialog();setFormState(initialFormState);setErrors({})}}>
                        <CancelRoundedIcon />
                      </IconButton>);
        title = `Thank you for signing up for our waiting list`;
        dialogForm = (<div>
                        <div className="form-row sp-flex-row-center">
                            <div className="input-box">
                                <span className="label">First Name</span>
                                <TextField
                                  id='first-name-id'
                                  className={`${errors.firstName ? 'input-error' : ''}`}
                                  fullWidth
                                  variant="outlined"
                                  type="text"
                                  name="firstName"
                                  // autoFocus
                                  required
                                  value={formState.firstName || ''}
                                  onChange={inputChangedHandler}/>
                                  { errors.firstName ? (
                                  <div className="error">
                                    {errors.firstName}
                                  </div>
                                  ) : null}
                            </div>
                            <div className="input-box">
                                <span className="label">Last Name</span>
                                <TextField
                                  className={`${errors.lastName ? 'input-error' : ''}`}
                                  fullWidth
                                  variant="outlined"
                                  type="text"
                                  name="lastName"
                                  required
                                  value={formState.lastName || ''}
                                  onChange={inputChangedHandler}/>
                                  { errors.lastName ? (
                                    <div className="error">
                                      {errors.lastName}
                                    </div>
                                  ) : null }

                            </div>
                        </div>
                        <div className="form-row input-box">
                            <span className="label">Your email address</span>
                            <TextField
                              className={`${errors.email ? 'input-error' : ''}`}
                              fullWidth
                              variant="outlined"
                              type="email"
                              name="email"
                              required
                              value={formState.email || ''}
                              onChange={inputChangedHandler}/>
                              { errors.email ? (
                                  <div className="error">
                                    {errors.email}
                                  </div>
                              ) : null}
                        </div>
                    </div>);
        button = (<Button className="btn" onClick={postDataHandler}>{submitButtonText}</Button>);
    }

    return (
    <Dialog
        className={isMobileOnly ? 'mobile' : ''}
        open={props.openDialog}
        aria-labelledby="form-dialog-title">
        {closeButton}
        <DialogTitle id="form-dialog-title">
            {title}
        </DialogTitle>
        <DialogContent>
            {txtContent}
            {dialogForm}
        </DialogContent>
        <DialogActions>
          {button}
        </DialogActions>
    </Dialog>
)}

export default SignUpDialog
